'use server';

import { redirect } from 'next/navigation';
import { cookies } from 'next/headers';
import { LoginSchema, RegisterSchema, type User } from '@/lib/definitions';
import { z } from 'zod';

// Mock function to simulate finding a user in the database
async function findUserByEmail(email: string): Promise<User | undefined> {
    // In a real app, this would query a database.
    if (email === 'user@example.com') {
        return { id: '1', email: 'user@example.com', username: 'Demo User' };
    }
    return undefined;
}

// Mock function to simulate creating a user
async function createUser(data: z.infer<typeof RegisterSchema>): Promise<User> {
    // In a real app, this would insert a new user into a database.
    console.log('Création de l\'utilisateur:', data.username);
    return { id: Date.now().toString(), email: data.email, username: data.username };
}

export async function login(prevState: any, formData: FormData) {
    const validatedFields = LoginSchema.safeParse(
        Object.fromEntries(formData.entries())
    );

    if (!validatedFields.success) {
        return {
            errors: validatedFields.error.flatten().fieldErrors,
        };
    }

    const { email, password } = validatedFields.data;

    // In a real app, you'd check the password hash. Here we'll just check for user existence and a mock password.
    const existingUser = await findUserByEmail(email);
    if (!existingUser || password !== 'password') {
        return {
            message: 'Email ou mot de passe invalide.',
        };
    }
    
    // In a real app, use an encrypted session library.
    cookies().set('session', JSON.stringify({ user: existingUser }), {
        httpOnly: true,
        secure: process.env.NODE_ENV === 'production',
        maxAge: 60 * 60 * 24 * 7, // One week
        path: '/',
    });

    redirect('/');
}

export async function register(prevState: any, formData: FormData) {
    const validatedFields = RegisterSchema.safeParse(
        Object.fromEntries(formData.entries())
    );

    if (!validatedFields.success) {
        return {
            errors: validatedFields.error.flatten().fieldErrors,
        };
    }
    
    const { email } = validatedFields.data;
    
    const existingUser = await findUserByEmail(email);
    if (existingUser) {
        return {
            message: 'Un compte avec cet e-mail existe déjà.',
        };
    }

    await createUser(validatedFields.data);

    redirect('/login?message=Inscription réussie. Veuillez vous connecter.');
}


export async function logout() {
  cookies().delete('session');
  redirect('/login');
}
